import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.sass']
})
export class HomeComponent implements OnInit {

  orderState = false;

  constructor() { }

  ngOnInit() {
  }

  changeOrderState() {
    this.orderState = !this.orderState;
  }

}



